
.wrapper{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    margin-left: auto;
    margin-right: auto;
    padding: 40px 20px;
    width: 450px;
    min-height: 450px;
    box-shadow: 2px 4px 51px 0px rgba(0, 0, 0, 0.75);
        -webkit-box-shadow: 2px 4px 51px 0px rgba(0, 0, 0, 0.75);
        -moz-box-shadow: 2px 4px 51px 0px rgba(0, 0, 0, 0.75);
}